import React from 'react';
import { connect } from 'react-redux';
import { REDUCER, submit } from 'ducks/login';
import { Form, Input, Button } from 'antd';
import { requiredRule } from '../../../../../utils/validateRules';

const FormItem = Form.Item;

const mapStateToProps = (state, props) => ({
  isSubmitForm: state.app.submitForms[REDUCER],
});

@connect(mapStateToProps)
@Form.create()
class LoginForm extends React.Component {
  static defaultProps = {};

  // $FlowFixMe
  onSubmit = isSubmitForm => event => {
    event.preventDefault();
    const { form, dispatch } = this.props;
    if (!isSubmitForm) {
      form.validateFields((error, values) => {
        if (!error) {
          dispatch(submit(values));
        }
      });
    }
  };

  render() {
    const { form, isSubmitForm } = this.props;

    return (
      <div className="cat__pages__login__block__form">
        <h4 className="text-uppercase">
          <strong>Đăng nhập</strong>
        </h4>
        <br />
        <Form layout="vertical" hideRequiredMark onSubmit={this.onSubmit(isSubmitForm)}>
          <FormItem label="Tên">
            {form.getFieldDecorator('username', {
              initialValue: '',
              rules: [requiredRule],
            })(<Input size="default" />)}
          </FormItem>
          <FormItem label="Mật khẩu">
            {form.getFieldDecorator('password', {
              initialValue: '',
              rules: [requiredRule],
            })(<Input size="default" type="password" />)}
          </FormItem>
          <div className="mb-2" />
          <div className="form-actions">
            <Button
              type="primary"
              className="width-150 mr-4"
              htmlType="submit"
              loading={isSubmitForm}
            >
              Đăng nhập
            </Button>
          </div>
        </Form>
      </div>
    );
  }
}

export default LoginForm;



